import '../../css/header/NavBar.css';
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

let NavBar = () => {
    let navigate = useNavigate();
    const [scrollPosition, setScrollPosition] = useState(0);
    const updateScroll = () => {
        setScrollPosition(window.scrollY || document.documentElement.scrollTop); // IE ... .scrollTop 
    };

    useEffect(() => {
        window.addEventListener('scroll', updateScroll);

        return () => {
            window.removeEventListener('scroll', updateScroll); //unmount 
        };
    }, []);

    return (
        <div
            className={`navbar ${ scrollPosition <= 50 ? 'not-scrolled' : 'scrolled' }`}
            style={{ position: 'fixed', top: '0', zIndex: '999' }}>
            <div className="logo" onClick={() => { navigate('/') }}>
                <p></p>
                <p></p>
            </div>

            <div className="menu">
                <ul>
                    <p onClick={() => { navigate('/MainInfo') }}></p>
                    <li onClick={() => { navigate('/MainInfo') }}></li>
                    <li onClick={() => { navigate('/LookAround') }}></li>
                    <li onClick={() => { navigate('/Location') }}></li>
                </ul>
                <ul>
                    <p onClick={() => { navigate('/SURO') }}></p>
                    <li onClick={() => { navigate('/SURO') }}></li>
                    <li onClick={() => { navigate('/KIMHAE') }}></li>
                    <li onClick={() => { navigate('/SINEO') }}></li>
                </ul>
                <ul>
                    <p onClick={() => { navigate('/ReservationInfo') }}></p>
                    <li onClick={() => { navigate('/ReservationInfo') }}></li>
                    <li onClick={() => { navigate('/Reservation') }}></li>
                </ul>
            </div>
        </div>
    );
}

export default NavBar;